.select-option {
  cursor: pointer;
  background-color: #fff;
  width: 100%;
  display: inline-block;
  position: relative;
}

.select-option .select-option-trigger {
  padding: 10px;
}

.select-option .select-option-items {
  z-index: 1000;
  background-color: #fff;
  border: 1px solid #ccc;
  border-top: none;
  border-radius: 0 0 4px 4px;
  width: 100%;
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
}

.select-option .select-option-items .select-option-item {
  border-bottom: 1px solid #ccc;
  padding: 10px;
}

.select-option .select-option-items .select-option-item:last-child {
  border-bottom: none;
}

.select-option .select-option-items .select-option-item--selected {
  color: #fff;
  background-color: #007bff;
}

.select-option--open .select-option-items {
  display: block;
}

.select-option select {
  display: none;
}
